.App_Wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #fc6767;
  height: 100vh;
}

.pdfconverterWrapper {
  min-height: 90vh;
  width: 72%;
  background: white;
  padding: 2.9rem 1.9rem;
  border-radius: 12px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.selectSectionWrapper {
  width: 100%;
  flex-direction: column;
  display: flex;
  justify-content: center;
  padding: 2.2rem 0;
  align-items: center;
  border: 3px dashed #e1e1e1;
  border-radius: 9px;
  position: relative;
  transition: all .2s ease-in-out ;
}

.selectSectionWrapper:hover {
  border-color: #b8b5b5;
  cursor: pointer;
}

.fileForUpload {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}

.image-container {
  width: fit-content;
  max-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container img {
  width: 100%;
}

button.downloadButton {
  background: transparent;
  border: 2px solid pink;
  border-radius: 22px;
  padding: 8px 29px;
  margin-top: 12px;
}
button.downloadButton:hover {
  cursor: pointer;
}

.selectFile {
  background-color: #1473e6;
  border: none;
  color: white;
  padding: 10px 33px;
  border-radius: 30px;
  font-weight: 600;
  font-size: 14px;
}

.fileForUpload {
  cursor: pointer;
}

.logoInner {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.pdfconverterWrapper span {
  font-weight: 700;
  font-size: 18px;
}

.pdfconverterWrapper h1 {
  color: #4b4b4b;
  font-size: 44px;
  margin: 0;
  margin-bottom: 1rem;
}

@media screen and (max-width:800px) {
  .App_Wrapper{
    height: fit-content!important;
 min-height: 100vh;
 padding: 1rem;
 justify-content: start;
  }
  .image-container{
    max-width: 80%;
  }
  .pdfconverterWrapper{
    width:100%!important;
  }
}


@media screen and (max-width:450px) {
  .pdfconverterWrapper{
    padding: 1.5rem 1.2rem;
  }
  .pdfconverterWrapper h1{
    font-size: 20px;
  }
  .imgForUpload{
    width: 100%!important;
  }
  .image-container{
    max-width: 100%;
  }
  .selectSectionWrapper p{
    text-align: center;
  }
}